.App {
  text-align: center;
}

.calendar-display {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
}

.calendar-display div {
  -ms-flex-preferred-size: 14.2857%;
      flex-basis: 14.2857%;
  background-color: beige;
  border:1px solid #ccc;
  padding: 7px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  text-align: left;
}

.calendar-display div[data-previous-month] {
  background-color: white;
}

.calendar-display div[data-next-month] {
  background-color: white;
}

.calendar-display span {
  -ms-flex-preferred-size: calc(100% - 14px);
      flex-basis: calc(100% - 14px);
}



